import "./header.css";
import HeaderLink from "./headerLink";

const Header = (props) => {
  return (
    <header>
      <HeaderLink
        onClick={() => props.setPage(0)}
        texto="Orar por otros"
        className={props.page === 0 ? "active" : ""}
      />
      <HeaderLink
        onClick={() => props.setPage(1)}
        texto="Hacer peticion"
        className={props.page === 1 ? "active" : ""}
      />
    </header>
  );
};

export default Header;
