import { useEffect, useState } from "react";
import "./formulario.css";
import { db, storage } from "../firebase";
import icono from "./add-image.svg";
import ReCAPTCHA from "react-google-recaptcha";
import Loading from "../Loading/loading";

const imagenesArray = [
  "https://i.pinimg.com/564x/ac/e0/1b/ace01bf624688cb3309544f09d8a3f1c.jpg",
  "https://i.pinimg.com/564x/79/ac/09/79ac09357e9ae6b33621b698ef23bb8c.jpg",
  "https://i.pinimg.com/564x/87/52/c1/8752c1e653eb99acc4801e6b0ecf3423.jpg",
  "https://i.pinimg.com/564x/cb/28/ff/cb28ffbbdca24dbfa115d2334e7b1aa2.jpg",
  "https://i.pinimg.com/564x/d2/db/28/d2db28c872c1f8548c05078a1f9d5575.jpg",
];

const Formulario = (props) => {
  const [nombre, setNombre] = useState("");
  const [peticion, setPeticion] = useState("");
  const [imagen, setImagen] = useState(null);
  const [imagenCambiada, setImagenCambiada] = useState(false);
  const [imagenPreviewUrl, setImagenPreviewUrl] = useState(
    imagenesArray[Math.floor(Math.random() * imagenesArray.length)]
  );
  const [enviado, setEnviado] = useState(0);
  const [peticionCounter, setPeticionCounter] = useState(0);
  const [captchaPassed, setCaptchaPassed] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    if (
      nombre !== "" &&
      peticion !== "" &&
      peticionCounter <= 255 &&
      captchaPassed
    ) {
      if (imagenCambiada) {
        handleImageUpload();
      } else {
        addPeticion(imagenPreviewUrl);
      }
    } else {
      alert("Por favor llenar todos los campos");
      console.log("No andes hackeando pls");
    }
  };

  const handleImageChange = (event) => {
    if (event.target.files[0]) {
      setImagenCambiada(true);
      setImagen(event.target.files[0]);
      setImagenPreviewUrl(URL.createObjectURL(event.target.files[0]));
    }
  };

  const handleImageUpload = () => {
    const uploadTask = storage.ref(`images/${imagen.name}`).put(imagen);
    uploadTask.on(
      "state_changed",
      (snapshot) => {},
      (error) => {
        console.log(error);
      },
      () => {
        storage
          .ref("images")
          .child(imagen.name)
          .getDownloadURL()
          .then((url) => {
            addPeticion(url);
          });
      }
    );
  };

  const addPeticion = async (url) => {
    setEnviado(1);
    await db
      .collection("peticiones")
      .add({
        name: nombre,
        pray: peticion,
        image: url,
      })
      .then((docRef) => {
        setEnviado(2);
        setTimeout(function () {
          props.setPage(0);
        }, 2000);
      })
      .catch((error) => {
        alert(error.message);
      });
  };

  function onChange(value) {
    if (value !== null) {
      setCaptchaPassed(true);
    } else {
      setCaptchaPassed(false);
    }
  }

  useEffect(() => {
    setPeticionCounter(peticion.length);
  }, [peticion.length]);

  return (
    <>
      {enviado === 0 && (
        <div className="formulario-container">
          <form onSubmit={handleSubmit}>
            <input
              id="imagen"
              className="input-imagen"
              type="file"
              onChange={handleImageChange}
            />
            {imagenPreviewUrl && (
              <label htmlFor="imagen" className="input-imagen__button">
                <div className="input-imagen__button__overlay">
                  <img src={icono} />
                </div>
                <img className="imagen-preview" src={imagenPreviewUrl} />
              </label>
            )}
            <input
              className="input-nombre"
              maxLength="27"
              type="text"
              name="nombre"
              placeholder="Nombre"
              onChange={(event) => setNombre(event.target.value)}
            />
            <div className="form-body">
              <textarea
                rows={8}
                type="text"
                name="nombre"
                placeholder="Petición"
                onChange={(event) => {
                  setPeticion(event.target.value);
                }}
              />
              {peticionCounter <= 255 ? (
                <span>{peticionCounter}/255</span>
              ) : (
                <span>
                  <span style={{ color: "red" }}>{peticionCounter}</span>/255
                </span>
              )}
              <div className="captcha-container">
                <ReCAPTCHA
                  sitekey="6Lcrx5wUAAAAAFivqPGaNfKEJiTi-YSdFfOJPes6"
                  onChange={onChange}
                />
              </div>
              <button
                className="form-button"
                disabled={
                  nombre === "" ||
                  peticion === "" ||
                  peticionCounter > 255 ||
                  !captchaPassed
                }
              >
                Enviar
              </button>
            </div>
          </form>
        </div>
      )}
      {enviado === 1 && (
        <Loading />
      )}
      {enviado === 2 && (
        <div className="formulario-container">
          <h2>Petición enviada correctamente 🙏🏻</h2>
        </div>
      )}
    </>
  );
};

export default Formulario;
