import { useEffect, useState } from "react";
import "./grupoTarjetas.css";
import { db } from "../firebase";
import Masonry, { ResponsiveMasonry } from "react-responsive-masonry";
import Loading from "../Loading/loading";

// Config variables
const GrupoTarjetas = () => {
  const [peticiones, setPeticiones] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const getPeticiones = async () => {
    setIsLoading(true);

    const response = db.collection("peticiones");
    const data = await response.get();
    data.forEach((peticion) => {
      setPeticiones((peticiones) => [...peticiones, peticion.data()]);
    });

    setIsLoading(false);
  };

  useEffect(() => {
    getPeticiones();
  }, []);

  return (
    <div className="grupo-tarjetas">
      {isLoading ? (
        <Loading />
      ) : (
        <>
          <ResponsiveMasonry
            columnsCountBreakPoints={{ 350: 1, 767: 3, 1023: 5, 1439: 7 }}
          >
            <Masonry>
              {peticiones.map((item, key) => (
                <div key={key} className="tarjeta-peticion">
                  <img className="tarjeta-peticion__imagen" src={item.image} />
                  <div className="tarjeta-peticion__nombre">{item.name}</div>
                  <div className="tarjeta-peticion__body">
                    <div className="tarjeta-peticion__peticion">
                      {item.pray}
                    </div>
                  </div>
                </div>
              ))}
            </Masonry>
          </ResponsiveMasonry>
          <div></div>
        </>
      )}
    </div>
  );
};

export default GrupoTarjetas;
